<template>
    <div @click="closeModal" class="modal">
    <div class="modal-inner">
    <div @click="closeModal" class="modal__close">&#10006;</div>
      <div class="modal__content">
        <h2 class="modal__title">Онлайн-заявка</h2>
        <p class="modal__text">Заполните форму, и мы вскоре свяжемся с вами,<br> чтобы ответить на все вопросы</p>
        <div class="modal__inf">

         <label class="modal__label-input"><input @change="validateTel" :value="date.telephone" @focus="isTel = true" class="modal__input" type="tel" placeholder="+7(995) 443 34 56">
         <p class="modal__label-text"
         :style="label.telephone"
         >
            &#10004;
         </p>
         </label>

         <label class="modal__label-input"><input @change="validateText" :value="date.name" class="modal__input" type="text" placeholder="Имя">
         <p class="modal__label-text"
         :style="label.text"
         >
            &#10004;
         </p>
         </label>
          <span v-if="isTel" class="modal__inf-tel span">Телефон *</span>
          <span  class="validate__tel validate">{{contextHelp.telephone}}</span>
          <span  class="validate__text validate">{{contextHelp.text}}</span>
        </div>
        <div class="modal__person">
            <p class="modal__person-text">Нажимая на кнопку «Оставить заявку», <strong> я даю <br>согласие на обработку персональных данных </strong> </p>
            <button @click="buttonSubmit" :disabled="disabled" class="modal__person-button button"><p  :class="animate">{{buttonText}}</p></button>
        </div>
        <div class="icon">
            <div class="icon__item"><a href="#"><img src="../assets/Shape.png"  title="Иконка" alt="Иконка"></a></div>
            <div class="icon__item"><a href="#"><img src="../assets/Vector.png" title="Иконка" alt="Иконка"></a></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>


export default{

    data(){
        return{
            isTel:false,
            date:{
                name:'',
                telephone:'',
            },

            disabled:true,

            validate:{
                name:'',
                telephone:'',
            },
            label:{
                text:{
                    display: 'none'
                },
                telephone:{
                    display: 'none',
                },
            },
            contextHelp:{
                text:'',
                telephone:'',
            },
            animate:{
                animane__button:false,
            },
            buttonText:'Оставить заявку'
        }
    },
    methods:{
        closeModal(event){
            if(event.target.classList[0]=='modal' || event.target.classList[0]=='modal__close'){
            this.$emit('closeModal')
            }
        },
        visibilities(){
            this.isTel = true 
        },
        buttonSubmit(){
            this.buttonText = ''
            this.animate.animane__button = true
            setTimeout(()=>{
            this.$emit('buttonSubmit',this.date)
            this.$emit('closeModal')
            },2000)

        },

        validateTel(event){
            if(event.target.value.length<11){
                this.date.telephone = ''
                this.contextHelp.telephone = 'Номер слишком маленький'
                return 
            }else if(event.target.value[0]!= '7'){
                this.date.telephone = ''
                this.contextHelp.telephone = 'Номер должен начинатся с 7'
                return 
            }else{
                this.contextHelp.telephone = '';
                this.date.telephone = event.target.value;
                this.label.telephone.display = 'inline-block'
            }
             if(this.date.name.length>1 && this.date.telephone.length>1){
                this.disabled = false
            }
        },


        validateText(event){
            if(event.target.value.length < 3){
                this.date.name = ''
                this.contextHelp.text = 'Слишком короткое имя'
            }else{
                this.date.name = event.target.value
                this.contextHelp.text = ''
                this.label.text.display = 'inline-block'
            }
            if(this.date.name.length>1 && this.date.telephone.length>1){
                this.disabled = false
            }
        }
    },
    
}
</script>




<style scoped lang="less">
 .animane__button {
            width: 30px;
            height: 30px;
            border-top: 4px;
            border-color: #fff;
            border-radius: 50%;
            border-style: solid;
            border-right: 4px;
            border-right-color: #fff;
            border-top: 4px;
            border-top-color: #fff;
            animation-name: but;
            animation-duration: 0.9s;
            animation-iteration-count: infinite;
}
 
        @keyframes but {
            from {
                transform: rotate(0deg);
            }
            to {
                transform: rotate(360deg);
            }
        }

.validate{
    position: absolute;
    bottom: 0;
    display: inline-block;
}
.validate__tel{
    left: 10px;
    color: rgba(213, 50, 52, 1);
    transform: translateY(100%);
}
.validate__text{
    right: 20%;
    color: rgba(213, 50, 52, 1);
    transform: translateY(100%);
}
.span{
    position: absolute;
    top: 2px;
    display: block;
    font-size: 1.2rem;
}
.modal__inf-name{
    right: 40%;
}
.modal__inf-tel{
    left: 4%;
}
.modal__input:focus{
    background-color: #fff;
}
.modal{
  z-index: 10000000000000;
  position: absolute;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, .4);
  &-inner{
    padding: 4rem;
    background-color: #fff;
    position: relative;
    border-radius: 25px 25px 25px 25px;
    top: 20%;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
  }
  &__close{
  position: absolute;
  font-size: 2rem;
  top: 10%;
  right: 10%;
  cursor: pointer;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(130, 130, 130, 1);
  background-color: rgba(243, 243, 244, 1);
  }
  &__content{
      position: relative;
      padding: 2rem;
      margin: 15rem 3rem  0 0;
  }
  &__title{
    font-size: 4rem;
    line-height:48,6px
  }
  &__text{
      font-size: 1.6rem;
      line-height: 2.4rem;
      margin-top: 2rem;
  }
   &__inf{
       position: relative;
       margin-top: 4rem;
       font-size: 1.6rem;
   }
   &__input{
       border-radius: 20px;
       padding: 1.3rem 2rem 0.5rem ;
       font-weight: bold;
       border-color: #FF9514;
       width: 300px;
       outline:none;
       transition:  background-color 0.1s;
       background-color: #F3F3F4;
   }
   
   &__person{
       display: flex;
       justify-content: space-between;
       border-radius: 10px;
       background-color: #F3F3F4;
       padding: 2.8rem 3.3rem;
       
       margin-top: 3rem;
       border: 1px solid rgba(243, 243, 244, 1);
       &-text{
           font-size: 1.4rem;
           margin-right: 5rem;
       }
       &-button{

        background-color: rgba(255, 149, 20, 1);
       color: #fff;
       border: 0;
       &:hover{
           background-color: rgba(17, 17, 17, 1);
           color: #fff;
           transition: background-color 0.3s ;
       }
       &:disabled{
           cursor:default;
           background-color: rgba(255, 149, 20, 0.4);
       }
       }
   }
   &__label-text{
       background-color: #77DE8D;
       border-radius: 5px;
       width: 20px;
       height: 20px;
       text-align: center;
       color: #fff;
       transform: translateX(-30px);
       display: inline-block;
   }
   &__label-input{
       margin-left: 3rem;
       &:first-child{
           margin-left: 0rem;
       }
   }
}

.icon{
    display: flex;
    margin-top: 5rem;
    align-items: center;
    justify-content: center;
    &__item{
        border-radius: 50%;
        width: 50px;
        height: 50px;
        border: 2px solid rgba(243, 243, 244, 1);
        margin-right: 2rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

@media(max-width: 769px){
    .modal-inner{
        padding: 2rem;
    }
    .modal__label-input{
        margin-left: 1rem;
    }
}
@media(max-width: 552px){
    .modal-inner{
        padding: 0rem;
    }
    .modal__content{
        margin: 0;
        padding: 1rem;
    }
    .modal__label-input{
        margin-left: 0;
        margin-top: 1rem;
    }
    .modal__input{
        margin-top: 3.5rem;
    }
    .modal__close{
        top: 2%;
        width: 30px;
        height: 30px;   
    }
    .modal__title{
        display: inline-block;
        font-size: 2rem;
    }
    .modal__text{
        font-size: 1.4rem;
        color: rgba(87, 87, 87, 1);
    }
    .modal__inf{
        margin-top: 0.7rem;
    }
    .modal__person{
        background-color: transparent;
        flex-wrap: wrap;
        margin-top: 0rem;
    }
    .modal__person-text{
            margin-right: 0rem;
            flex: 1 1 100%;
            order: 2;
            text-align: center;
            margin-top: 1.6rem;
    }
    .modal__person-button{
        flex: 1 1 100%;
    }
    .icon{
        margin-top: 0;
    }
    .modal__close{
        z-index: 100000;
    }
    .validate__tel{
        bottom: 53%;
    }
    .validate__text{
        left: 10px;
    }
}
</style>