<script>
import ModalVue from './components/modal' 
import Slider from './components/slider' 
export default {
  name: 'App',
  components: {
    ModalVue,
    Slider
  },
  data(){
    return{
      mainDate:{
        price:'',
        first:'',
        term:'',
        sum:'',
        payment:'',

      },
      price: +'1000000',
      first: +'105048',
      term: +'1',
      Min:'100000',
      Max:'600000',
      isModal:false,
      headerBurger:{
        display:'flex',
      },

    }
  },
  methods:{
    newTerm(event){
      this.term = Math.floor(+event.target.value);
    },
    newFirst(event){
      this.first = Math.floor((+event.target.value/this.term)*this.term);
      
    },
    newPrice(event){
      this.price = Math.floor(+event.target.value)
      this.Min=+this.price*0.1
      this.Max=+this.price*0.6
      if(this.first < this.price*0.1){
        this.first = Math.floor(this.price*0.1)
      }else if(this.first > this.price*0.6){
        this.first = Math.floor(this.price*0.6)
      }
    },
    compliteDate(date){
      Object.assign(this.mainDate,date)


      this.mainDate.price = this.price
      this.mainDate.first = this.first
      this.mainDate.term  = this.term
      this.mainDate.sum = this.summ
      this.mainDate.payment = this.payment


      let obj = JSON.stringify(this.mainDate)
      let xhr = new XMLHttpRequest();
      xhr.open('POST','lorem')
      xhr.send(obj);

    },
    headerOpen(){
      this.headerBurger.display = 'flex'
    },
    closeHeaderMenu(){
        this.headerBurger.display = 'none'
    }

  },
  computed:{
    payment(){
      return Math.floor(this.price - (+this.first)*(13/(1+13)-this.term-1))
    },
    summ(){
      return Math.floor(this.first+(this.payment*this.term))
    },
  },
  watch:{
  }
}
</script>





