<template>
    <section class="slider">
        <div class="container">
            <div class="main">
                <div :style="{marginLeft: sliderMargin.marginLeft + 'px',}" class="main-slider">
              <div class="slider-inner">
                <div class="slider__text">
                    <h2 class="slider__title">Авто в лизинг для<br> физических лиц</h2>
                    <p class="sloder__text">Получите машину за 5 дней</p>
                    <button class="button slider__button" @click="sliderCrick">Оставить заявку</button>
                </div>
                <picture>
                    <source @load="newImage" srcset="../assets/05.jpg" media="(max-width: 552px)" class="slider__img">
                    <source @load="newImage" srcset="../assets/04.jpg" media="(max-width: 771px)" class="slider__img">
                    <img @load="newImage" class="slider__img" src="../assets/Frame122191.jpg" alt="Машина" title="Машина">
                </picture>
                <span @click="nextSlide" class="arrow next ">&#11166;</span>
                <span @click="prefSlide" class="arrow pref disabled">&#11164;</span>
              </div>
              <div class="slider-inner">
                <div class="slider__text">
                    <h2 class="slider__title">Авто в лизинг для<br> Юридических лиц</h2>
                    <p class="sloder__text">Получите машину за 5 дней</p>
                    <button class="button slider__button" @click="sliderCrick">Оставить заявку</button>
                </div>
                <picture>
                    <source @load="newImage" srcset="../assets/05.jpg" media="(max-width: 552px)" class="slider__img">
                    <source @load="newImage" srcset="../assets/04.jpg" media="(max-width: 771px)" class="slider__img">
                    <img @load="newImage" class="slider__img" src="../assets/Frame122191.jpg" alt="Машина" title="Машина">
                </picture>
                <span @click="nextSlide(0)" class="arrow next">&#11166;</span>
                 <span @click="prefSlide(1)" class="arrow pref">&#11164;</span>
              </div>
              <div class="slider-inner">
                <div class="slider__text">
                    <h2 class="slider__title">У нас есть свой<br> калькулятор</h2>
                    <p class="sloder__text">Получите машину за 5 дней</p>
                    <button class="button slider__button" @click="sliderCrick">Оставить заявку</button>
                </div>
                <picture>
                     <source @load="newImage" srcset="../assets/05.jpg" media="(max-width: 552px)" class="slider__img">
                    <source @load="newImage" srcset="../assets/04.jpg" media="(max-width: 771px)" class="slider__img">
                    <img @load="newImage" class="slider__img" src="../assets/Frame122191.jpg" alt="Машина" title="Машина">
                </picture>
                <span @click="nextSlide"  class="arrow next disabled">&#11166;</span>
                <span @click="prefSlide" class="arrow pref">&#11164;</span>
              </div>
            </div>
            <div class="slider-menu">
                <div @click="itemClick(0)" :class="menu[0]"  class="menu-item"></div>
                <div @click="itemClick(1)" :class="menu[1]" class="menu-item"></div>
                <div @click="itemClick(2)" :class="menu[2]" class="menu-item"></div>
            </div>
            </div>
        </div>
    </section>
</template>


<script>
export default{

    data(){
      return{  
          t:'.',
        sliderMargin:{
           marginLeft:0,
        },
        vidthImg:0,
        num:2,
        menu:[
           {active: true},
           {active: false},
           {active: false},
        ],
        indx:0,
      }
    },

    methods:{
        sliderCrick(){
            this.$emit('clickSlider')
        },
        nextSlide(){
            console.log(this.indx);
            if(-this.num * this.vidthImg <= this.sliderMargin.marginLeft*this.num){
            this.sliderMargin.marginLeft -= this.vidthImg
            this.menu[this.indx].active = false
            this.menu[this.indx+1].active = true
            this.indx += 1
            }
        },
         prefSlide(){
             if(this.sliderMargin.marginLeft != 0){
            this.sliderMargin.marginLeft += this.vidthImg
            this.menu[this.indx].active = false
            this.menu[this.indx-1].active = true
            this.indx -= 1
            }
        },
        newImage(event){
            this.vidthImg = event.target.width;
            return  
        },
        itemClick(value){
            this.sliderMargin.marginLeft = -this.vidthImg * value
            this.menu.map(item=>{
                item.active = false
            },)
            this.menu[value].active = true
        },
    },
    computed:{
         
    },
    watch:{
    }
}
</script>



<style scoped lang="less">


.disabled{
    opacity: 0.4;
    cursor: default;
}

.main{
    width: 1344px;
    overflow: hidden;
    position: relative;
    max-width: 100%;
}
h2{
    display: inline-block;
}
.main-slider{
    transition: margin-left 500ms;
    width: 9999999999999px;
    display: flex;
}
.menu-item{
    width: 10px;
    height: 10px;
    background-color: rgb(138, 138, 138);
    border-radius: 50%;
    margin-right: 2rem;
    cursor: pointer;
}
.menu-item.active{
    background-color: #fff;
    width: 12px;
    height: 12px;
}
.slider {
 padding: 1rem;
display: block;

    &-menu{
        position: absolute;
        left: 50%;
        bottom: 10%;
        display: flex;
    }
    &__button{
        align-self: baseline;
        margin-top: 2rem;
        background-color: #FF9514;
        color: #fff;
        border: 1px solid rgba(255, 149, 20, 1);
        &:hover{
        transition: background-color 0.3s;
        background-color: #000;
        border: none;
        }
        &:focus{
            outline: none;
        }
    }
}
        
.slider__img {
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    max-width: 100%;
    width: 1304px;
}
        
.slider-inner {
    position: relative;
    background-size: 100%;
}
        
.slider__text {
    color: #fff;
    position: absolute;
    z-index: 100;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 7rem 7rem 0;
}
        
.slider__title {
    margin: 0;
    font-size: 4.4rem;
}
        
.sloder__text {
    margin-top: 3rem;
    font-size: 2.8rem;
}



.arrow{
  position: absolute;
  
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid  rgba(88, 88, 88, 0.3);;
  cursor: pointer;
  transition: all 0.3s ease-in;
  border-radius: 50%;
    &:hover{
   background-color:  rgba(88, 88, 88, 0.3);
  }
 
}
.next{
  right: 30px;
  font-size: 3rem;
  width: 50px;
  height: 50px;
  bottom: 20px;
  &:hover{
  border-right: 2px solid #fff;
  }
}
.pref{
  width: 30px;
  height: 30px;
  font-size: 2.5rem;
  right: 90px;
  bottom: 26px;
   &:hover{
     border-left: 2px solid #fff;;
   }
}

@media (max-width: 1025px){
    .slider__img{
        width: 940px;
        height: 437px;
    }
    .slider__text{
        padding: 5rem 5rem 0;
    }
    .slider__title{
        font-size: 4rem;
    }
    .sloder__text{
        font-size: 2.5rem;
    }
}
@media (max-width: 769px){
    .container{
        padding: 0;
    }
    .slider__img{
        width: 747px;;
       height: 460px;
    }
    .slider__text{
        padding: 9rem 15rem 0 0;
    }
    .slider__title{
        font-size: 4rem;
    }
    .sloder__text{
        display: none;
    }
    .slider-inner{
        display: flex;
    justify-content: center;
    align-items: center;
    }

}


@media (max-width: 552px){
    .arrow{
        display: none;
    }
    .slider__title{
        font-size: 3rem;
        line-height: 25px;
    }
    .slider__img{
        width: 320px;
    }
    .slider{
        padding: 0;
        transform: translateY(-12%);
    }
    .slider__text{
        padding: 10rem 2rem 0;
    }
    .slider__button{
        margin-top: 3rem;
    }
    .slider-menu{
        transform: translateX(-50%);
    }
}
</style>